import { Component } from '@angular/core';
import { AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-reversion-list',
  templateUrl: 'reversion-list.html',
})
export class ReversionListPage {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  v1:any;
  v2:any;
  v3:any;
  v4:any;
  sidist:boolean=false;
  tiposus: any=[];
  pingreso:boolean=false;
  pconsulta:boolean=false;
  peliminacion:boolean=false;
  pcreacion:boolean=false;
  pactualizacion:boolean=false;
  preporte:boolean=false;
  pfiltro:boolean=false;
  permisom: any=[];
  idempresa: any;
  idafiliado:any='na';

  idsuscriptor: any;
  username: string = "";
  nidsuscriptor: any;
  
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';

  constructor( public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {
      this.permisom = String(navParams.get("permiso")).split(",");
      this.pingreso=(  this.permisom[0] == 'true');
      this.pconsulta=(  this.permisom[1] == 'true');
      this.peliminacion=(  this.permisom[2] == 'true');
      this.pcreacion=(  this.permisom[3] == 'true');
      this.pactualizacion=(  this.permisom[4] == 'true');
      this.pfiltro=(  this.permisom[5] == 'true');
      this.preporte=(  this.permisom[6] == 'true');
     
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  
      

  pideregistros() { 
      let self = this;
      self.loading = true;

      
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
  this.idsuscriptor=this.tiposus[3]
/*
  if(this.tiposus[0]=='Distribuidor')
  {
    this.sidist=true;
  }
  else
  {
    this.sidist=false;
  }

*/
  this.regService.getReg( this.idsuscriptor + '/' + this.idempresa,'reversionsaldos').then((data) => {
 
    this.items=data;
    self.loading = false;
}, (err) => {
  this.msj.showError3('Error',err.error);
});
});


     
  }
  ionViewDidLoad() {
    

    //this.idsuscriptor=this.navParams.data.record.nodpi;
    //this.nidsuscriptor=this.navParams.data.record.nombre +' '+ this.navParams.data.record.apellido;

    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
        
    this.pideregistros();
     
  })


    this.translateService.get(["CANCEL",
    "INGRESAR",
    "BUSCARPOR",
    "BUSCAR"
  ]).subscribe(
    (values) => {
     this.v1=values.CANCEL
     this.v2=values.INGRESAR
     this.v3=values.BUSCARPOR
     this.v4=values.BUSCAR
    });

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 

  

  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  
  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'reversionsaldos').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
        
      
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


    
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('ReversionCreatePage', { record : item },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('ReversionCreatePage',{ record : null },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
  // this.items.concat([{idcomprasaldo:data.idcomprasaldo,idsuscriptor:data.idsuscriptor,fecha:data.fecha,monto:data.monto,nombre:data.noplaca}]);
      }  
    })
    addModal.present();
      //this.navCtrl.push('EmpresaCreatePage');
   }
   viewRecord(item : any): void
   {
     // let modal = this._MODAL.create(ViewGalleryPage, { record : item });
     // modal.present();
   }

   excludeTracks: any = [];

   presentFilter() {
    
  }

  

}



